<template>
    <div ref="solidBox" id="solidBox" :v-model="dataPopRadio" class="solidBox" @scroll="scrollChange" @mousemove="scrollEnd">
        <ul ref="scrollUl">
            <li
                    v-for="(item, index) in dataPopOption"
                    :key="index"
                    @click="pickerCurrent(index)"
                    :class="index===0?'active':'active2'"
            >{{item.label}}</li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: 'scrollPicker',
        props: {
            rangStart: {
                type: Number
            },
            rangEnd: {
                type: Number
            },
            step: {
                type: Number,
                default: 1
            },
            unit: {
                type: String
            },
            value: {
                type: Number
            }
        },
        data() {
            return {
                dataPopRadio: '',
                dataPopOption: []
            }
        },
        beforeMount() {
            for (let i = this.rangStart; i <= this.rangEnd; i += this.step) {
                this.dataPopOption.push({ label: i + this.unit, value: i })
            }
        },
        mounted() {
            this.$refs.solidBox.scrollTop = this.value * 40
        },
        methods: {
            scrollChange(e) {
                // é¦åè·åå°liåç´ 
                const lis = this.$refs.scrollUl.getElementsByTagName('li')
                // è·åå½åä¸­å¿çli
                const currentLi = Math.round(e.target.scrollTop / 40)
                // å¯¹liåç´ è¿è¡éå
                for (let i = 0; i < lis.length; i++) {
                    // å¯¹æ¯æ¯å¦æ¯å½åactiveå±æ§
                    if (i === currentLi) {
                        // å¦ææ¯ç»åç´ å¢å "active"çå±æ§
                        lis[i].classList.remove('active2')
                        lis[i].classList.add('active')
                        // ç»å½ååç´ ä»¥ä¸çåç´ å¨æè®¾ç½®æå­å¤§å°ï¼ç¦»å½åæå­è¶è¿ï¼æå­å¤§å°è¶å°
                        for (let j = 1; j <= i; j++) {
                            lis[i - j].style.fontSize = (14 - j) + 'px'
                        }
                        // ç»å½ååç´ ä»¥ä¸çåç´ å¨æè®¾ç½®æå­å¤§å°ï¼ç¦»å½åæå­è¶è¿ï¼æå­å¤§å°è¶å°
                        for (let a = 1; a < lis.length - i; a++) {
                            lis[i + a].style.fontSize = (14 - a) + 'px'
                        }
                        // å½æ²¡æ»å¨å°æå¾ä½ç½®æ¶ï¼ä¹å°±æ¯å¶ä»ææåç´ ï¼ç§»é¤"active"çæ ·å¼ï¼å¢å "active2"çæ ·å¼
                    } else {
                        lis[i].classList.remove('active')
                        lis[i].classList.add('active2')
                    }
                }
            },
            // é¼ æ ç§»å¨æ¶å°é«äº®èç¹ç§»è³æ­£ä¸­å¿
            scrollEnd() {
                // é¦åè·åå°liåç´ 
                const lis = this.$refs.scrollUl.getElementsByTagName('li')
                // // å¯¹liåç´ è¿è¡éå
                for (let i = 0; i < lis.length; i++) {
                    if (lis[i].classList[0] === 'active') {
                        // ä¿®æ¹å½åå¼ä¼ éç»ç¶ç»ä»¶
                        this.$emit('input', this.dataPopOption[i].value)
                        // å°é«äº®èç¹ç§»è³ä¸­å¿
                        this.$refs.solidBox.scrollTop = i * 40
                    }
                }
            },
            pickerCurrent(ind) {
                // éè¿é¼ æ ä½¿éæ©é¡¹ä¸ºå½åé«äº®é¡¹
                this.$refs.solidBox.scrollTop = ind * 40
            }
        }
    }
</script>

<style scoped>
    .solidBox{
        height: 200px;
        width: 60px;
        overflow-y: scroll;
        touch-action: pan-y;
        -webkit-overflow-scrolling: touch;
    }
    .solidBox::-webkit-scrollbar {
        display: none;
    }
    ul {
        padding: 80px 0 80px 0;
        margin: 0;
        background-color: #fff;
    }
    li {
        user-select: none;
        list-style: none;
        line-height: 40px;
        font-size: 13px !important;
        text-align: center;
        opacity: 0.3;
        height: 40px;
        background-color: #fff;
        cursor: pointer;
    }
    .active {
        font-weight: 400;
        font-size: 14px !important;
        color: #6383ff;
        opacity: 1.2 !important;
        top: 150px;
    }
    .active2 {
        color: #333;
        opacity: 0.6 !important;
    }
    .active2:hover{
        background: #f5f7fa;
    }
</style>
